export default {
        data() {
            return {
				content:"",
				choose:[],
				title: 'request-payment',
				loading: false,
				price: 1,
				providerList: [],
				orderInfo:[],
            }
        },
		onload(){
			uni.getProvider({
			    service: "payment",
			    success: (e) => {
			        console.log("payment success:" + JSON.stringify(e));
			        let providerList = [];
			        e.provider.map((value) => {
			            switch (value) {
			                case 'alipay':
			                    providerList.push({
			                        name: '支付宝',
			                        id: value,
			                        loading: false
			                    });
			                    break;
			                case 'wxpay':
			                    providerList.push({
			                        name: '微信',
			                        id: value,
			                        loading: false
			                    });
			                    break;
			                default:
			                    break;
			            }
			        })
			        this.providerList = providerList;
			    },
			    fail: (e) => {
			        console.log("获取支付通道失败：", e);
			    }
			});
		},
		
        methods: {
            checkboxChange: function (e) {
					
                    this.choose = e.detail.value;
                console.log(this.choose);
            },
			send(){
				
				if (!this.choose.length ) {
					uni.showToast({
					    title: '请先选择发送对象！',
					    duration: 2000,
						icon:"none"
					});
				}else{
					var index = 0;
					
					while(index < this.choose.length){
						if(this.choose[index] == "people"){
							console.log(this.choose[index]);
							this.setPeople();
						}else if(this.choose[index] == "worker"){
							console.log(this.choose[index]);
							this.setWorker();
						}
						index++;
					}
				}
				
			},
			setPeople(){
				var that = this;
				uni.request({
					url: 'http://106.13.239.202/index.php?c=admin&m=setPeople',
					method: 'POST',
					header: {"content-type": "application/x-www-form-urlencoded"},
					data: {
						
					},
					success: res => {
						 if(res.data.status == "OK"){
						 	uni.showToast({
						 	    title: '发送给业主成功！',
						 	    duration: 2000,
								icon:"none"
						 	});
						 }
					       
					},
					fail: () => {
					
					},
					complete: () => {
						
					}
				});
			},
			pay(){
				var that = this;
				uni.request({
					url: 'http://192.168.43.239/wxpay.php',
					method: 'GET',
					header: {"content-type": "application/x-www-form-urlencoded"},
					data: {
						
					},
					success: res => {
						 
					       console.log(res.data);
						   var obj = {
						   	"appid":res.data.appid,
						   	"noncestr": res.data.noncestr,
						   	"package": "Sign=WXPay",
						   	"partnerid": res.data.appid,
						   	"prepayid": res.data.prepayid,
						   	"timestamp": res.data.timestamp,
						   	"sign": res.data.sign
						   }
						   				
						   
						   var orderInfo = JSON.stringify(obj);
						   console.log(orderInfo);
						   uni.requestPayment({
						       provider: 'wxpay',
						       orderInfo: obj, //微信、支付宝订单数据
						       success: function (res) {
						           console.log('success:' + res);
						       },
						       fail: function (err) {
						           
						   		uni.showModal({
						   		    content: "支付失败,原因为: " + err.errMsg,
						   		    showCancel: false
						   		})
						       }
						   });
					}
				});

				
				
			},
			setWorker(){
				var that = this;
				uni.request({
					url: 'http://106.13.239.202/index.php?c=admin&m=setWorker',
					method: 'POST',
					header: {"content-type": "application/x-www-form-urlencoded"},
					data: {
						content:that.content
					},
					success: res => {
						if(res.data.status == "OK"){
							console.log("成功")
							uni.showToast({
							    title: '发送给工作人员成功！',
							    duration: 2000,
								icon:"none"
							});
						}
						  
					       
					},
					fail: () => {
					
					},
					complete: () => {
						
					}
				});
			},
			bindTextAreaBlur: function (e) {
						this.content = e.detail.value;
			            console.log(this.content)
			},
			
        }
    }
